export function randomArray() {
    let array = [];
    let max = 10;
    let i = 0;
    while (i <= max) {
        let randomNumber = Math.floor(Math.random() * 30)
        array.push(randomNumber)
        i++
    }
    //console.log(array)
    return array
}

